import { useEffect, useContext } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { Formik } from "formik";
import * as Yup from "yup";
import { InputBox, PasswordBox, ButtonBox, CheckBox, Buttons, SelectBox, MultiSelectBox } from "../../../components/RenderFroms";
import { BiSolidUser, BiSolidLockAlt, BiRightArrowAlt } from "react-icons/bi";
import Breadcrumbs from "../../../components/Breadcrumbs";
import ListTitle from "../../../components/ListTitle";
import { useFetch } from "../../../contexts/useFetch";
import { useFetchSingle } from "../../../contexts/useFetchSingle";
import { usePost } from "../../../contexts/usePost";
import { usePatch } from "../../../contexts/usePatch";
const resource = "customers";

const defaultValue = {
	companyName: "",
	customerName: "",
	customerEmailId: "",
	customerAccountAdminName: "",
	customerAccountAdminEmailId: "",


}

export default function AddCustomer() {
	const navigate = useNavigate();
	const { id } = useParams();

	const validationSchemaInfo = Yup.object().shape({
		companyName: Yup.string().required("This is required"),
		customerName: Yup.string().required("Modules is required"),
		customerEmailId: Yup.string().required('This is required'),


	});

	const { fetchSingle, data: customers, loading: loadingData } = useFetchSingle();
	useEffect(() => {
		fetchSingle(resource, id)
	}, [id])

	const { create, data: respond, loading } = usePost();
	const { edit, data: respond1, loading: loading1 } = usePatch();
	const handleUpdate = (values: any) => {
		if (id) {
			edit(resource, values)
		} else {
			create(resource, values)
		}
	}

	useEffect(() => {
		if ((respond as any)?.status == 201) {
			navigate(-1)
		}
		if ((respond1 as any)?.status == 200) {
			navigate(-1)
		}
	}, [respond, respond1])

	if (loadingData) {
		return <>loading</>
	}

	return (
		<>

			<Breadcrumbs root="customers" pageName={`${id ? 'Edit' : 'Add'} Customers`} />
			<div className="main-content-body">
				<div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
					<div className="row">
						<div className="card">

							<ListTitle ListName={`${id ? 'Edit' : 'Add New'} Customer (* indicates mandatory field(s)).`} />


							<div className="form-horizontal has-validation-callback">
								<Formik
									initialValues={customers ? customers : defaultValue}
									validationSchema={validationSchemaInfo}
									onSubmit={(values: any) => handleUpdate(values)}
								>
									{({ handleChange, handleBlur, handleSubmit, values, errors }) => (

										<>
											<div className="card-body">

												<InputBox
													required={true}
													name="companyName"
													lable="Company Name"
													placeholder=""
												/>

												<InputBox
													required={true}
													name="customerName"
													lable="Customer Name"
													placeholder=""
												/>

												<InputBox
													required={true}
													name="customerEmailId"
													lable="Customer Email ID"
													placeholder=""
												/>

												<InputBox

													name="customerAccountAdminName"
													lable="Customer Account/Admin Name"
													placeholder=""
												/>

												<InputBox

													name="customerAccountAdminEmailId"
													lable="Customer Account/Admin Email ID"
													placeholder=""
												/>
												<div className="clear"></div>
											</div>


											<div className="card-action">
												<label htmlFor="AddGroup_Submit" className="col-lg-4 col-md-4 col-sm-12 col-xs-12 control-label">&nbsp;</label>
												<div className="col-lg-8 col-md-8 col-sm-12 col-xs-12 no-padding pb-3">
													<Buttons loading={loading || loading1} className="btn-success" value="Save Customer" onClick={() => handleSubmit()} />
													<Buttons className="btn-grey" value="Go Back to List" onClick={() => navigate(-1)} />

												</div>
											</div>
											<div className="clear">&nbsp;</div>

										</>)}
								</Formik>
							</div>


						</div>
					</div>
				</div>
			</div>


		</>
	);
}


